.PokemonInfo {
  font-family: Arial, Helvetica, sans-serif;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: auto;
  opacity: 1;
  transition: all ease-in-out 0.5s;
}

.PokemonInfo_close {
  font-family: Arial, Helvetica, sans-serif;
  height: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: auto;
  opacity: 0;
  transition: all ease-in-out 0.5s;
}

.PokemonInfo__header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 0;
}

.PokemonInfo__header-image {
  width: 30%;
}

.PokemonInfo__title {
  font-size: 30px;
  margin: 5px;
}

.PokemonInfo__image {
  width: 80%;
  border: white 2px solid;
  border-radius: 10px;
  margin: 0;
  padding: 0;
}

.PokemonInfo__info-container {
  width: 80%;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  margin: 10px 0;
  padding: 5px;
}

.PokemonInfo__body-container {
  background-color: #ebebeb;
  border-radius: 10px;
  margin: 5px;
  padding: 0 5px;
}

.PokemonInfo__body-p {
  font-size: 18px;
  font-weight: 600;
  margin: 10px 0;
  padding: 0;
}

.PokemonInfo__type-container {
  display: flex;
  flex-direction: column;
  background-color: #ebebeb;
  border-radius: 10px;
  margin: 5px;
  padding: 0 5px;
}

.PokemonInfo__type-h2 {
  font-size: 26px;
  margin: 10px 0;
  padding: 0;
}

.PokemonInfo__type-type {
  display: flex;
  flex-direction: row;
  gap: 5px;
  padding: 0 0 10px;
}

.type {
  font-size: 18px;
  line-height: 20px;
  color: white;
  margin: 0;
  padding: 3px 10px;
  border-radius: 5px;
}

.PokemonInfo__abilities-container {
  display: flex;
  flex-direction: column;
  background-color: #ebebeb;
  border-radius: 10px;
  margin: 5px;
  padding: 0 5px;
}

.PokemonInfo__ability-ability {
  display: flex;
  flex-direction: row;
  gap: 5px;
  padding: 0 0 10px;
}

.PokemonInfo__ability-name {
  font-size: 18px;
  line-height: 20px;
  color: black;
  background-color: #dddddd;
  margin: 0;
  padding: 3px 10px;
  border-radius: 5px;
}

.PokemonInfo__stats {
  width: 80%;
  display: flex;
  flex-direction: column;
  background-color: #ebebeb;
  border-radius: 10px;
  margin: 5px 0 15px;
  padding: 10px 5px 20px;
}

.stats-h2 {
  align-self: center;
  text-align: center;
  margin: 0;
  padding: 0;
}

.PokemonInfo__stats-name {
  margin: 10px 1px 2px;
  padding: 0;
}

.PokemonInfo__loading {
  width: 80%;
  margin: auto;
  padding: 0;
}

@media screen and (max-width:500px) {
  .PokemonInfo {
    width: 100%;
  }

  .PokemonInfo__header-image {
    width: 20%;
  }

  .PokemonInfo__title {
    font-size: 26px;
    margin: 5px;
  }
  
  .PokemonInfo__image {
    width: 90%;
  }

  .PokemonInfo__info-container {
    width: 90%;
  }

  .PokemonInfo__body-p {
    font-size: 16px;
  }

  .PokemonInfo__type-h2 {
    font-size: 22px;
  }

  .type {
    font-size: 16px;
    line-height: 18px;
  }

  .PokemonInfo__ability-name {
    font-size: 16px;
    line-height: 18px;
  }

  .PokemonInfo__stats {
    width: 90%;
  }
}
 
